import { mapState } from "vuex";

export default {
    data() {
        return {
            iphone_x: false,
            card: [],
            list: [],
            modal: false,
            page_loading: true,
            to_add: false,
            num: 1,
            beCard: null,
            index: -1,
            dialog: false,
            newIndex: 0,
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin
        })
    },
    methods: {
        showDialog() {
            this.dialog = !this.dialog;
        },
        getCard() {
            let that = this;
            that.$request({
                url: that.$api.app_admin.card,
            }).then(response=>{
                that.$hideLoading();
                if(response.code === 0) {
                    that.cat = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        },
        bindChange(e) {
            this.newIndex = e.detail.value[0];
        },
        toggle(status) {
            this.dialog = false;
            if (status == 1) {
                this.beCard = this.card[this.newIndex];
                this.newIndex = 0;
            }
        },
        close(index) {
            this.list.splice(index, 1);
        },
        submit() {
            if (this.beCard != null) {
                this.beCard.num = this.num;
                if(this.index > -1) {
                    this.list[this.index] = this.beCard;
                }else {
                    this.list.push(this.beCard);
                }
                this.num = null;
                this.to_add = false;
            }else {
                uni.showToast({
                    title: '请选择卡券',
                    icon: 'none',
                    duration: 1000
                });
            }
        },
        save() {
            this.$storage.setStorageSync('goods_card', this.list);
            setTimeout(function() {
                uni.navigateBack();
            }, 500);
        },
        cancel() {
            this.to_add = false;
            this.beCard = null;
        },
        add(index,item) {
            this.$request({
                url: this.$api.app_admin.card,
            }).then(response => {
                this.$hideLoading();
                if(response.code === 0) {
                    let card = response.data.list;
                    this.num = 1;
                    if (card.length > 0) {
                        this.to_add = true;
                        if(index > -1) {
                            this.beCard = this.list[index];
                            this.num = item.num;
                            this.index = index;
                            for(let i in card) {
                                if(card[i].id == this.beCard.id) {
                                    this.newIndex = i;
                                }
                            }
                        }else {
                            this.index = -1;
                            this.newIndex = 0;
                            this.beCard = null;
                        }
                    } else {
                        uni.showToast({
                            title: '暂无可添加卡券',
                            icon: 'none',
                            duration: 1000
                        });
                    }
                    this.card = card;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                this.$hideLoading();
            });
        },
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        this.list = this.$storage.getStorageSync('goods_card') ? this.$storage.getStorageSync('goods_card') : [];
        uni.getSystemInfo({
            success: function (res) {
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                    that.iphone_x = true;
                }
            }
        })
        that.getCard();
    }
}
